import { Table, TableRow, WidthType, AlignmentType, BorderStyle } from "docx";
import { Colors } from "../../constants";
import { IContributionTime } from "../../types";
import generateTableCell from "../docx/generateTableCell";

const generateTableRow = (possibilityOfRetirement: IContributionTime) => {
  const row = new TableRow({
    children: [
      generateTableCell({
        text: possibilityOfRetirement.key,
        color: Colors.blue,
        fill: Colors.green,
        width: 50,
        bold: true,
        font: "Mensch ExtBd",
      }),
      generateTableCell({
        text: possibilityOfRetirement.value,
        color: Colors.blue,
        fill: Colors.green,
        width: 50,
        font: "Bahnschrift",
      }),
    ],
  });

  return row;
};

const contributionTimesDataTable = (contributionTimes: IContributionTime[]) => {
  const rows: TableRow[] = [];

  for (let i = 0; i < contributionTimes.length; i++) {
    const row = generateTableRow(contributionTimes[i]);
    rows.push(row);
  }

  const table = new Table({
    alignment: AlignmentType.CENTER,
    width: {
      size: 100,
      type: WidthType.PERCENTAGE,
    },
    margins: {
      top: 100,
      bottom: 100,
      left: 100,
      right: 100,
    },
    borders: {
      top: {
        space: 300,
        color: "auto",
        style: BorderStyle.SINGLE,
        size: 6,
      },
    },
    columnWidths: [1000],
    rows: rows,
  });

  return table;
};

export default contributionTimesDataTable;
