import { Table, TableRow, WidthType, AlignmentType, BorderStyle } from "docx";
import { Colors } from "../../constants";
import { IComparation } from "../../types";
import generateTableCell from "../docx/generateTableCell";

const generateTableRow = (comparation: IComparation) => {
  const row = new TableRow({
    children: [
      generateTableCell({
        text: comparation.rule,
        color: Colors.blue,
        fill: Colors.green,
        width: 20,
        bold: true,
        font: "Mensch ExtBd",
      }),
      generateTableCell({
        text: comparation.contributionSalary,
        color: Colors.blue,
        fill: Colors.green,
        width: 20,
      }),
      generateTableCell({
        text: comparation.monthlyInvestment,
        color: Colors.blue,
        fill: Colors.green,
        width: 20,
      }),
      generateTableCell({
        text: comparation.totalInvestment,
        color: Colors.blue,
        fill: Colors.green,
        width: 20,
      }),
      generateTableCell({
        text: comparation.returnOnInvestment,
        color: Colors.blue,
        fill: Colors.green,
        width: 20,
      }),
    ],
  });

  return row;
};

const comparationDataTable = (contributionTimes: IComparation[]) => {
  const rows: TableRow[] = [];

  const header = new TableRow({
    children: [
      generateTableCell({
        text: "Regra",
        color: Colors.blue,
        fill: Colors.green,
        width: 20,
        bold: true,
        font: "Mensch ExtBd",
      }),
      generateTableCell({
        text: "Salrio de Contribuio",
        color: Colors.blue,
        fill: Colors.green,
        width: 20,
        bold: true,
        font: "Mensch ExtBd",
      }),
      generateTableCell({
        text: "Investimento Mensal",
        color: Colors.blue,
        fill: Colors.green,
        width: 20,
        bold: true,
        font: "Mensch ExtBd",
      }),
      generateTableCell({
        text: "Investimento Total",
        color: Colors.blue,
        fill: Colors.green,
        width: 20,
        bold: true,
        font: "Mensch ExtBd",
      }),
      generateTableCell({
        text: "Retorno sobre o Investimento",
        color: Colors.blue,
        fill: Colors.green,
        width: 20,
        bold: true,
        font: "Mensch ExtBd",
      }),
    ],
  });

  rows.push(header);

  for (let i = 0; i < contributionTimes.length; i++) {
    const row = generateTableRow(contributionTimes[i]);
    rows.push(row);
  }

  const table = new Table({
    alignment: AlignmentType.CENTER,
    width: {
      size: 100,
      type: WidthType.PERCENTAGE,
    },
    margins: {
      top: 100,
      bottom: 100,
      left: 100,
      right: 100,
    },
    borders: {
      top: {
        space: 300,
        color: "auto",
        style: BorderStyle.SINGLE,
        size: 6,
      },
    },
    columnWidths: [1000],
    rows: rows,
  });

  return table;
};

export default comparationDataTable;
