import React from 'react'
import { Input, Label, TitleFile } from './styles'
import {FaUpload} from 'react-icons/fa'

interface IProps {
  onChange: (value: React.ChangeEvent<HTMLInputElement> | undefined) => void;
  file: File | null | undefined;
}

const FileInput = ({ onChange, file }: IProps)  => {
  return(
    <>
      <Label htmlFor='file'>
        <FaUpload size={100} />
        {file ? (
          <TitleFile>{file.name}</TitleFile>          
        ): <TitleFile>Selecione o Arquivo...</TitleFile>}
      </Label>
      <Input accept='application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' type="file" id='file' onChange={onChange} />
    </>
  )
}

export default FileInput