import React from "react";
import FileInput from "../../components/file-input";
import { Column, Content, Main } from "../../styles";
import readXlsxFile, { Row } from "read-excel-file";
import Button from "../../components/button";
import {
  Document,
  Packer,
  Paragraph,
  ImageRun,
  TextRun,
  AlignmentType,
} from "docx";
import { saveAs } from "file-saver";
import userDataTable from "../../utils/userDataTable";
import retirementOfPossibilityDataTable from "../../utils/retirementOfPossibilityDataTable";
import { LogoContainer } from "./styles";
import { Colors, Texts } from "../../constants";
import generateSubtitle from "../../utils/docx/generateSubtitle";
import generateSimpleText from "../../utils/docx/generateSimpleText";
import contributionTimesDataTable from "../../utils/contributionTimeDataTable";
import { IComparation, IContributionTime } from "../../types";
import comparationDataTable from "../../utils/comparationDataTable";
import ComparationChart from "../../components/comparation-chart";
import html2canvas from "html2canvas";
import { renderToStaticMarkup } from "react-dom/server";

const keys = {
  userName: "Nome do Segurado:",
  nit: "N de Identificao do Trabalhador (NIT):",
  cpf: "CPF:",
  birthDay: "Data de Nascimento:",
  age: "Idade",
  contributionTime: "Tempo de Contribuio",
  ec: "EC 103",
  dib: "DIB Informada",
  possibleRetirement: "Possvel Aposentadoria:",
  percentage: "% Contribuies:",
};

const formatBLR = (value: string) => {
  const newValue = parseFloat(value).toLocaleString("pt-br", {
    style: "currency",
    currency: "BRL",
  });

  return newValue;
};

const getValueCell = (
  key: string,
  rows: Row[],
  indexValueX = 0,
  indexValueY = 0
) => {
  const indexRow = rows.findIndex((row) => row.includes(key));
  const indexCell = rows[indexRow].findIndex((value) => value === key);

  const value = rows[indexRow + indexValueX][indexCell + indexValueY];

  return value.toString();
};

const getIndexesMultipleKey = (key: string, rows: Row[], key2: string = "") => {
  const indexes = rows
    .map((row, index) => {
      if (
        row.filter((value) => value && value.toString().includes(key)).length >
          0 &&
        key2.length > 0 &&
        row.filter((value) => value && value.toString().includes(key2)).length >
          0
      ) {
        return index;
      }

      if (
        row.filter((value) => value && value.toString().includes(key)).length >
          0 &&
        key2.length === 0
      ) {
        return index;
      }

      return undefined;
    })
    .filter((value) => value !== undefined);

  return indexes;
};

const getIndexesKey = (key: string, rows: Row[]) => {
  const indexes = rows
    .map((row, index) => {
      if (row.includes(key)) {
        return index;
      }

      return undefined;
    })
    .filter((value) => value !== undefined);

  return indexes;
};

const possibilityOfRetirement = (rows: Row[]) => {
  const contributionsTime = [];
  const indexes = getIndexesKey(keys.contributionTime, rows);

  for (let i = 0; i < indexes.length; i++) {
    const idx = indexes[i];
    if (idx !== undefined) {
      const cellIdx = rows[idx].findIndex(
        (value) => value.toString() === keys.contributionTime
      );
      const contributionTime = {
        key: rows[idx][cellIdx].toString(),
        name: rows[idx - 1][cellIdx].toString(),
        age: rows[idx + 1][cellIdx].toString(),
        value: rows[idx + 2][cellIdx + 7].toString(),
        date: rows[idx + 4][cellIdx].toString(),
      };

      contributionsTime.push(contributionTime);
    }
  }

  return contributionsTime;
};

const contributionTime = (rows: Row[]) => {
  const indexesEc = getIndexesMultipleKey(keys.contributionTime, rows, keys.ec);
  const indexesDib = getIndexesMultipleKey(
    keys.contributionTime,
    rows,
    keys.dib
  );
  const ecIndex = indexesEc[0];
  const dibIndex = indexesDib[0];
  const contributionTimes: IContributionTime[] = [];

  if (ecIndex) {
    const cellIndex = rows[ecIndex].findIndex(
      (value) =>
        value &&
        value.toString().includes(keys.contributionTime) &&
        value.toString().includes(keys.ec)
    );

    const key = rows[ecIndex][cellIndex]
      ? rows[ecIndex][cellIndex].toString()
      : "";
    const value = rows[ecIndex + 1][cellIndex]
      ? rows[ecIndex + 1][cellIndex].toString()
      : "";

    contributionTimes.push({
      key: key,
      value: value,
    });
  }

  if (dibIndex) {
    const cellIndex = rows[dibIndex].findIndex(
      (value) =>
        value &&
        value.toString().includes(keys.contributionTime) &&
        value.toString().includes(keys.dib)
    );
    const key = rows[dibIndex][cellIndex]
      ? rows[dibIndex][cellIndex].toString()
      : "";
    const value = rows[dibIndex + 1][cellIndex + 1]
      ? rows[dibIndex + 1][cellIndex + 1].toString()
      : "";

    contributionTimes.push({
      key: key,
      value: value,
    });
  }

  return contributionTimes;
};

const comparation = (rows: Row[]) => {
  const key = "contribuies de R$ ";
  const keyReturnOnInvesment = " = R$ ";
  const eq = "Nenhum investimento necessrio";
  const percentageIndex = getIndexesKey(keys.percentage, rows)[0];
  let percentageValue = "";
  if (percentageIndex) {
    const cellIndex = rows[percentageIndex].findIndex(
      (value) => value && value.toString() === keys.percentage
    );
    percentageValue = rows[percentageIndex + 1][cellIndex].toString();
  }
  const percentage = parseInt(percentageValue);

  const indexes = getIndexesKey(keys.possibleRetirement, rows);
  const comparations: IComparation[] = [];

  for (let i = 0; i < indexes.length; i++) {
    const index = indexes[i];

    if (index) {
      const totalInvestment = rows[index + 3][2].toString();
      const contributionValue = totalInvestment.split(key);
      if (contributionValue) {
        const returnOnInvestmentValue = rows[index + 5][2]
          .toString()
          .split(keyReturnOnInvesment);
        const idxInvestment = returnOnInvestmentValue.length === 1 ? 0 : 1;
        let returnOnInvestment = returnOnInvestmentValue[idxInvestment]
          .replace(/\./g, "")
          .replace(",", ".");
        const contribution =
          totalInvestment === eq
            ? "0"
            : parseFloat(
                contributionValue[1].replace(/\./g, "").replace(",", ".")
              ).toFixed(2);
        const monthlyInvestment = Math.abs(
          (parseFloat(contribution) * percentage) / 100
        ).toFixed(2);

        returnOnInvestment = returnOnInvestment.replace("R$ ", "");

        comparations.push({
          rule: rows[index - 1][0].toString(),
          contributionSalary: formatBLR(contribution),
          monthlyInvestment: formatBLR(monthlyInvestment),
          totalInvestment: totalInvestment,
          returnOnInvestment: formatBLR(returnOnInvestment),
        });
      }
    }
  }

  return comparations;
};

const getUser = (rows: Row[]) => {
  return {
    name: getValueCell(keys.userName, rows, 1),
    cpf: getValueCell(keys.cpf, rows, 1),
    nit: getValueCell(keys.nit, rows, 1),
    birthday: getValueCell(keys.birthDay, rows, 1),
    age: getValueCell(keys.age, rows, 1),
  };
};

const space = () => {
  const paragraph = new Paragraph("");

  return paragraph;
};

const transformComponentToImage = async (comparations: IComparation[]) => {
  const chart = ComparationChart({ data: comparations });
  const html = document.createElement("div");
  const element = renderToStaticMarkup(chart);

  html.style.position = "absolute";
  html.style.zIndex = "-100";
  html.innerHTML = `${element}`;

  document.body.appendChild(html);

  const canvas = await html2canvas(html);
  const data = canvas.toDataURL("image/jpg", 1.0);

  document.body.removeChild(html);

  return data;
};

const Home = () => {
  const [file, setFile] = React.useState<File | null | undefined>();

  const onChange = async (
    value: React.ChangeEvent<HTMLInputElement> | undefined
  ) => {
    if (value?.target.files) {
      setFile(value.target.files[0]);
    }
  };

  const generate = async () => {
    if (!file) {
      console.log("Nenhum arquivo encontrado.");
      window.alert("Nenhum arquivo encontrado.");
      return;
    }

    try {
      const blobLogo = await (
        await fetch("https://i.ibb.co/wR9DV4M/logo.png")
      ).blob();
      const bufferLogo = await blobLogo.arrayBuffer();

      // data read from multiple tabs
      const rowsA = await readXlsxFile(file);
      const rowsB = await readXlsxFile(file, { sheet: "B" });
      const rowsD = await readXlsxFile(file, { sheet: "D" });

      // data read from excel and formatted
      const user = getUser(rowsA);
      const retirementPossibilities = possibilityOfRetirement(rowsA);
      const contributionsTime = contributionTime(rowsB);
      const comparations = comparation(rowsD);

      // file components
      const userTable = userDataTable(user);
      const retirementsOfPossibilitiesTable = retirementOfPossibilityDataTable(
        retirementPossibilities
      );
      const contributionTimesTable =
        contributionTimesDataTable(contributionsTime);
      const comparationTable = comparationDataTable(comparations);

      //chart
      const chartUrl = await transformComponentToImage(comparations);

      const doc = new Document({
        styles: {
          characterStyles: [
            {
              name: "Bahnschrift",
              id: "Bahnschrift",
            },
          ],
        },
        sections: [
          {
            properties: {
              page: {
                margin: {
                  top: "1.27cm",
                  right: "1.27cm",
                  bottom: "1.27cm",
                  left: "1.27cm",
                },
              },
            },
            children: [
              new Paragraph({
                children: [
                  new ImageRun({
                    data: bufferLogo,
                    transformation: {
                      width: 75,
                      height: 75,
                    },
                  }),
                ],
              }),
              new Paragraph({
                children: [
                  new TextRun({
                    text: "Planejamento Previdencirio",
                    size: "22pt",
                    font: "Mensch ExtBd",
                    shading: { color: Colors.blue },
                    color: Colors.blue,
                  }),
                ],
                alignment: AlignmentType.CENTER,
              }),
              space(),
              userTable,
              space(),
              generateSimpleText(Texts[0]),
              space(),
              generateSubtitle("Tempo de Contribuio"),
              space(),
              contributionTimesTable,
              space(),
              generateSimpleText(Texts[1]),
              space(),
              generateSubtitle("Pendncias no CNIS"),
              space(),
              generateSimpleText(Texts[2]),
              space(),
              generateSubtitle("Possibilidades de Aposentadoria"),
              space(),
              generateSimpleText(Texts[3]),
              generateSimpleText(Texts[4]),
              generateSimpleText(Texts[5]),
              space(),
              retirementsOfPossibilitiesTable,
              space(),
              generateSubtitle("Comparativo"),
              space(),
              comparationTable,
              space(),
              generateSubtitle("Retorno sobre o Investimento"),
              space(),
              new Paragraph({
                children: [
                  new ImageRun({
                    data: chartUrl,
                    transformation: {
                      width: 560,
                      height: 200,
                    },
                  }),
                ],
                alignment: AlignmentType.CENTER,
              }),
              space(),
              generateSubtitle("Relatrio"),
              space(),
              generateSimpleText(Texts[6]),
            ],
          },
        ],
      });

      const blob = await Packer.toBlob(doc);
      console.log("Gerado com sucesso");

      saveAs(blob, "example.docx");
    } catch (err) {
      console.log(err);
      window.alert("Erro ao processar as informaes");
    }
  };

  return (
    <Main>
      <Content>
        <Column>
          <LogoContainer />
          <FileInput onChange={onChange} file={file} />
          <Button onClick={generate}>Gerar Relatrio</Button>
        </Column>
      </Content>
    </Main>
  );
};

export default Home;
