import { IComparation } from "../../types";

interface IProps {
  data: IComparation[];
}

const convertFloat = (value: string) => {
  const key = "R$";
  const newValue = value.split(key);
  value = newValue[1].replace(/\./g, "").replace(",", ".");
  return parseFloat(value);
};

const ComparationChart = ({ data }: IProps) => {
  let max = 0;

  data.forEach((value) => {
    const investment = convertFloat(value.returnOnInvestment);
    if (investment > max) {
      max = investment;
    }
  });

  const calcWidth = (value: string) => {
    const total = convertFloat(value);
    const width = (total * 100) / max;

    return `${width}%`;
  };

  const colors = [
    "#6D905C",
    "#E6A83B",
    "#425D75",
    "#6D905C",
    "#E6A83B",
    "#425D75",
    "#6D905C",
  ];

  return (
    <div style={{ display: "flex", flexDirection: "column" }}>
      {data.map((comparation, index) => (
        <div
          key={comparation.rule}
          style={{
            display: "flex",
            flexDirection: "row",
            width: 1000,
            height: 35,
            textAlign: "left",
            padding: 10,
          }}
        >
          <div style={{ width: "30%", display: "flex", alignItems: "center" }}>
            <label style={{ fontSize: 16, fontFamily: "Mensch ExtBd" }}>
              {comparation.rule}
            </label>
          </div>
          <div style={{ width: "70%", display: "flex", alignItems: "center" }}>
            <div
              style={{
                width: calcWidth(comparation.returnOnInvestment),
                backgroundColor: colors[index],
                height: "20px",
              }}
            ></div>
          </div>
        </div>
      ))}
      <div style={{ display: "flex", flexDirection: "row" }}>
        {data.map((comparation, index) => (
          <div
            key={index}
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              padding: 10,
            }}
          >
            <div
              style={{ width: 20, height: 20, backgroundColor: colors[index] }}
            ></div>
            <label style={{ paddingLeft: 5, fontWeight: "bold" }}>
              {comparation.returnOnInvestment}
            </label>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ComparationChart;
