import { Table, TableRow, WidthType, AlignmentType, BorderStyle } from "docx";
import { Colors } from "../../constants";
import { IPossilibityOfRetirement } from "../../types";
import generateTableCell from "../docx/generateTableCell";

const generateTableRow = (
  possibilityOfRetirement: IPossilibityOfRetirement
) => {
  const row = new TableRow({
    children: [
      generateTableCell({
        text: possibilityOfRetirement.name,
        color: Colors.blue,
        fill: Colors.green,
        width: 33,
        bold: true,
        font: "Mensch ExtBd",
      }),
      generateTableCell({
        text: possibilityOfRetirement.date,
        color: Colors.blue,
        fill: Colors.green,
        width: 33,
      }),
      generateTableCell({
        text: `R$ ${possibilityOfRetirement.value}`,
        color: Colors.blue,
        fill: Colors.green,
        width: 33,
      }),
    ],
  });

  return row;
};

const retirementPossibilitiesTable = (
  retirementPossibilities: IPossilibityOfRetirement[]
) => {
  const rows: TableRow[] = [];

  const header = new TableRow({
    children: [
      generateTableCell({
        text: "Regra",
        color: Colors.blue,
        fill: Colors.green,
        width: 33,
        bold: true,
        font: "Mensch ExtBd",
      }),
      generateTableCell({
        text: "Data Prevista",
        color: Colors.blue,
        fill: Colors.green,
        width: 33,
        bold: true,
        font: "Mensch ExtBd",
      }),
      generateTableCell({
        text: "Provvel RMI",
        color: Colors.blue,
        fill: Colors.green,
        width: 33,
        bold: true,
        font: "Mensch ExtBd",
      }),
    ],
  });

  rows.push(header);

  for (let i = 0; i < retirementPossibilities.length; i++) {
    const row = generateTableRow(retirementPossibilities[i]);
    rows.push(row);
  }

  const table = new Table({
    alignment: AlignmentType.CENTER,
    width: {
      size: 100,
      type: WidthType.PERCENTAGE,
    },
    margins: {
      top: 100,
      bottom: 100,
      left: 100,
      right: 100,
    },
    borders: {
      top: {
        space: 300,
        color: "auto",
        style: BorderStyle.SINGLE,
        size: 6,
      },
    },
    columnWidths: [1000],
    rows: rows,
  });

  return table;
};

export default retirementPossibilitiesTable;
