import {
  TableCell,
  WidthType,
  VerticalAlign,
  BorderStyle,
  TextRun,
  Paragraph,
} from "docx";

interface IGenerateTableCell {
  text: string;
  color: string;
  fill: string;
  bold?: boolean;
  width: number;
  font?: string;
}

const generateTableCell = ({
  color,
  fill,
  text,
  width,
  bold,
  font = "Bahnschrift",
}: IGenerateTableCell) => {
  const cell = new TableCell({
    width: {
      size: width,
      type: WidthType.PERCENTAGE,
    },
    verticalAlign: VerticalAlign.CENTER,
    shading: {
      color: color,
      fill: fill,
    },
    borders: {
      top: {
        color: "#ffffff",
        style: BorderStyle.SINGLE,
        size: 1,
      },
      bottom: {
        color: "#ffffff",
        style: BorderStyle.SINGLE,
        size: 1,
      },
      left: {
        color: "#ffffff",
        style: BorderStyle.SINGLE,
        size: 1,
      },
      right: {
        color: "#ffffff",
        style: BorderStyle.SINGLE,
        size: 1,
      },
    },
    children: [
      new Paragraph({
        children: [
          new TextRun({
            text: text,
            color: color,
            bold: bold,
            font: font,
            size: "14pt",
          }),
        ],
      }),
    ],
  });

  return cell;
};

export default generateTableCell;
