import { AlignmentType, Paragraph, TextRun } from "docx";
import { Colors } from "../../../constants";

const generateSimpleText = (text: string) => {
  const paragraph = new Paragraph({
    children: [
      new TextRun({
        children: [text],
        color: Colors.blue,
        size: "14pt",
        font: "Bahnschrift",
      }),
    ],
    indent: {
      firstLine: "1.25cm",
    },
    spacing: {
      after: 160,
      afterAutoSpacing: true,
    },
    alignment: AlignmentType.JUSTIFIED,
  });

  return paragraph;
};

export default generateSimpleText;
