import { Table, TableRow, WidthType, AlignmentType, BorderStyle } from "docx";
import { Colors } from "../../constants";
import { IUser } from "../../types";
import generateTableCell from "../docx/generateTableCell";

const generateRow = (
  label: string,
  value: string,
  fontLabel: string,
  fontValue: string
) => {
  const row = new TableRow({
    children: [
      generateTableCell({
        text: label,
        color: Colors.blue,
        fill: Colors.green,
        width: 35,
        bold: true,
        font: fontLabel,
      }),
      generateTableCell({
        text: value,
        color: Colors.blue,
        fill: Colors.green,
        width: 65,
        font: fontValue,
      }),
    ],
    tableHeader: true,
  });

  return row;
};

const userDataTable = (user: IUser) => {
  const table = new Table({
    alignment: AlignmentType.CENTER,
    width: {
      size: 100,
      type: WidthType.PERCENTAGE,
    },
    margins: {
      top: 100,
      bottom: 100,
      left: 100,
      right: 100,
    },
    borders: {
      top: {
        space: 300,
        color: "auto",
        style: BorderStyle.SINGLE,
        size: 6,
      },
    },
    columnWidths: [1000],
    rows: [
      generateRow("Segurado:", user.name, "Mensch ExtBd", "Bahnschrift"),
      generateRow("CPF:", user.cpf, "Mensch ExtBd", "Bahnschrift"),
      generateRow("NIT:", user.nit, "Mensch ExtBd", "Bahnschrift"),
      generateRow(
        "Data de nascimento::",
        user.birthday,
        "Mensch ExtBd",
        "Bahnschrift"
      ),
      generateRow("Idade:", user.age, "Mensch ExtBd", "Bahnschrift"),
    ],
  });

  return table;
};

export default userDataTable;
